<template>
  <div class="b" style="height: 100%; height: 100%">
    <div id="mainB" style="width: 100%; height: 100%"></div>
  </div>
</template>

<script>
import echarts from 'echarts'
require('echarts/theme/macarons')
export default {
  name: "radarChart",
   props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '300px'
    }
  },
  data(){
    return{
      option:{
      
        stompClient: '',
        ChartLineGraph:null,
        title: {
          text: ''
        },
         toolbox: {
               feature: {
               saveAsImage: {}
              }
            },
        legend: {
          data: ['', '']
        },
       radar: {
          // shape: 'circle',
        indicator: [
            { name: '1', max: 100},
            { name: '2', max: 100},
            { name: '1', max: 100},
            { name: '2', max: 100},
            { name: '1', max: 1},
            { name: '2', max: 1}
          ]
        },
        series: [{
          name: '',
          type: 'radar',
          data: [
            {
              value: [],
              name: '1'
            },
            {
              value: [],
              name: '2'
            }
          ]
        }]
      }
    }
  },
  methods:{
    setChart(){
     this.chart = echarts.init(this.$el, 'macarons')
      this.chart.setOption(
          this.option
      )
    }
  },
  mounted() {
        this.$nextTick(() => {
       this.setChart()
    })
  },
   beforeDestroy: function () {
    // ,
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
    this.disconnect();
  },
    //   
    //  ajax
// deep:true.datahttps://cn.vuejs.org/v2/api/#watch
      watch: {
    option: {
      handler() {
        this.chart.setOption(this.option);
        this.chart.resize()
      }, deep: true
    }
  },
}

</script>

