<template>
  <div class="a" style="height: 100%; height: 100%">
    <div id="mainA" style="width: 100%; height: 100%"></div>
  </div>
</template>

<script>
import  echarts from 'echarts'
require('echarts/theme/macarons')
export default {
  name: "pieChart",
   props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '300px'
    }
  },
  data(){
    return{
      option: {
        
         stompClient: '',
         ChartLineGraph:null,
         title: {
          text: ''
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          top: '5%',
          left: 'center'
        },
         toolbox: {
               feature: {
               saveAsImage: {}
              }
            },
        series: [
          {
            name: '',
            type: 'pie',
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 10,
              borderColor: '#fff',
              borderWidth: 2
            },
            label: {
              show: false,
              position: 'center'
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '40',
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: false
            },
            data: [
              {value: 1048, name: ''},
              {value: 580, name: ''},
              {value: 484, name: ''},
              {value: 300, name: ''}
            ]
          }
        ]
      }
    }
  },
  methods:{
    setChart(){
      this.chart = echarts.init(this.$el, 'macarons');
    this.chart.setOption(this.option);
    }
  },
  mounted() {
     this.$nextTick(() => {
    this.setChart()
    })
  },
   beforeDestroy: function () {
    // ,
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
    this.disconnect();
  },
    //   
    //  ajax
// deep:true.datahttps://cn.vuejs.org/v2/api/#watch
      watch: {
    option: {
      handler() {
        this.chart.setOption(this.option);
        this.chart.resize()
      }, deep: true
    }
  },
}
</script>

