<template>
  <div class="c" style="height: 100%; width: 100%">
    <div id="mainC" style="height: 100%; width: 100%;"></div>
  </div>
</template>

<script>
import echarts from 'echarts'
require('echarts/theme/macarons')
export default {
  name: "barChart",
     props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '300px'
    }
  },
  data(){
    return{
      stompClient: '',
      ChartLineGraph:null,
      option:{
         
        title: {
          text: ''
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['', '', '', '', '']
      },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: '',
            type: 'line',
            stack: '',
            
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: '',
            type: 'line',
            stack: '',
           
            data: [220, 182, 191, 234, 290, 330, 310]
          },
          {
            name: '',
            type: 'line',
            stack: '',
             
            data: [150, 232, 201, 154, 190, 330, 410]
          },
          {
            name: '',
            type: 'line',
            stack: '',
         
            data: [320, 332, 301, 334, 390, 330, 320]
          },
       
        ]
      }
    }
  },
  
  mounted() {
     this.$nextTick(() => {
      this.setChart()
    })
  },
  beforeDestroy: function () {
    // ,
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
    this.disconnect();
  },
    //   
    //  ajax
// deep:true.datahttps://cn.vuejs.org/v2/api/#watch
      watch: {
    option: {
      handler() {
        this.chart.setOption(this.option);
        this.chart.resize()
      }, deep: true
    }
  },
  methods: {
    setChart(){
        this.chart =echarts.init(this.$el, 'macarons');
     this.chart.setOption(this.option)
    }
  },
}

</script>

